export class Control{
    text : HTMLElement;
    output : HTMLElement;
    constructor(button : HTMLElement, input : HTMLElement, word : HTMLElement){
        this.text = input;
        this.output = word;
        button.addEventListener('click', ()=>this.get_text());
    }

    get_text(){
        let parent = document.getElementById('results'); //–ù–µ —Ç–∏–ø–∏–∑–∏—Ä—É–µ–º, –ø–æ—Ç–æ–º—É —á—Ç–æ –¥–µ–ª–∞–µ–º –ø—Ä–æ–≤–µ—Ä–∫—É
        if (!(parent instanceof HTMLElement))
            return ;
        let i : number= 0, 
        arr : string[] = [], word = this.text.textContent, newWord = word;
        if (!(word))
            return ;
        if (!(newWord))
            return ;
        let nid = document.getElementById('new');
        if (!(nid instanceof HTMLElement))
            return ;
        nid.classList.remove('hidden');
        let aid = document.getElementById('article');
        if (!(aid instanceof HTMLElement))
            return ;
        aid.classList.add('hidden');
        while (parent.firstChild)
        {
            parent.removeChild(parent.firstChild);
        }
        if ( word.slice(-1) > '–∞' &&  word.slice(-1) < '—è')
        {
            console.log( word.slice(-2));
            if ( word.slice(-2) === '–æ–≥'){
                arr[i] = '–∏–Ω—è';
            }
            else if( word.slice(-3) === '–Ω–∏–∫'){
                arr[i] = '–Ω–∏—Ü–∞';
                newWord = word.slice(0, -3);
                console.log(newWord);
            }
            else if( word.slice(-2) === '–∏–∫'){
                arr[i] = '–∏—Ü–∞';
                newWord = word.slice(0, -2);
                console.log(newWord);
            }
            else if( word.slice(-2) === '–µ—Ü'){
                arr[i] = '—á–∏—Ö–∞';
                newWord = word.slice(0, -2);
                console.log(newWord);
            }
            else if( word.slice(-1) === '—Ä' || word.slice(-1) === '–Ω'){
                arr[i] = '—à–∞';
                arr[i+1] = '–∫–∞';
                console.log(newWord);
            }
            else if( word.slice(-2) === '–ª—å'){
                arr[i] = '–Ω–∏—Ü–∞';
                console.log(newWord);
            }
            else if( word.slice(-1) === '—å'){
                arr[i] = '–∏—Ü–∞';
                arr[i+1] = '–∫–∞';
                newWord = word.slice(0, -1);
            }
            else {
                arr[i] = '–∏—Ü–∞';
                arr[i+1] = '–µ—Å—Å–∞';
                arr[i+2] = '–∏–Ω—è';
                arr[i+3] = '–∏—Ö–∞';
            }
            let arr2 = document.querySelectorAll('input[type=checkbox]'); 
            let arr3 : string[] = [];
            let t = -1;
            i = arr2.length;
            while (i--)
            {
                if (arr2[i].checked)
                {
                    let ltg = document.getElementsByTagName('label');
                    if (!(ltg instanceof HTMLElement))
                        return ;
                    let tmp = ltg[i].textContent;
                    if (!(tmp))
                        return ;
                    arr3[t+=1] = tmp.slice(1, -1);
                }
            }
            i = arr3.length;
            while (i--)
            {
                if (arr3[i].slice(-1) === '–Ω')
                    arr3[i] += '—è';
                else
                    arr3[i] += '–∞';
            }
            if (arr3.length)
                arr = arr.filter(item => arr3.includes(item));
            i = arr.length;
            if (i === 0){
                let newDiv = document.createElement('p');
                newDiv.innerHTML = '–¢–∞–∫–∏—Ö —Å–ª–æ–≤ –Ω–µ—Ç';
                parent.append(newDiv);
            }
            else
            {
                while (i--)
                 {
                let newDiv = document.createElement('p');
                newDiv.innerHTML =  newWord[0].toUpperCase() +  newWord.slice(1).toLowerCase() + arr[i] ;
                parent.append(newDiv);
                  }
            }
        }
        else if (  word.slice(-1) == '–∞' ||  word.slice(-1) == '—è')
        {
            let newDiv = document.createElement('p');
            newDiv.innerHTML = '–≠—Ç–æ —Å–ª–æ–≤–æ –∏ —Ç–∞–∫ –ø—Ä–µ–∫—Ä–∞—Å–Ω–æ';
            parent.append(newDiv);
        }
        else if ( word == '' )
        {
            const nid = document.getElementById('new');
            if (!(nid instanceof HTMLElement))
                    return ;
            nid.classList.add('hidden');
            const aid = document.getElementById('article');
            if (!(aid instanceof HTMLElement))
                    return ;
            aid.classList.remove('hidden');
            while (parent.firstChild)
            {
            parent.removeChild(parent.firstChild);
            } 
        }
        else
        {
            let newDiv = document.createElement('p');
            newDiv.innerHTML = '–ù—É –∏–∑–¥–µ–≤–∞—Ç—å—Å—è —Ç–æ –Ω–µ –Ω–∞–¥–æ';
            parent.append(newDiv);
        }
        
    }
}



