export class check{
    x1 = 0;
    constructor(button: HTMLElement){
        button.addEventListener('click', ()=>this.nextQuestion());
    }

    nextQuestion(){
        if (this.x1<10){
      // console.log('–ù—É –¥–æ–ø—É—Å—Ç–∏–º 3');
       let arr = ['–ê–¥–≤–æ–∫–∞—Ç', '–ù—è–Ω—å', '–î–æ–∫—Ç–æ—Ä', '–•–∏—Ä—É—Ä–≥', '–ú–∞—Ç–µ–º–∞—Ç–∏–∫', '–ö–æ–∫', '–í—Ä–∞—á', '–ü–µ–≤–µ—Ü', '–û—Ñ–∏—Ü–∏–∞–Ω—Ç', '–ë–∞—Ä—Ç–µ–Ω–¥–µ—Ä'];
       let parent = document.getElementById('comment');
       if (!(parent instanceof HTMLElement))
            return ;
       while (parent.firstChild)
            {
            parent.removeChild(parent.firstChild);
            } 
       let s = document.getElementsByClassName('play');
        let i = s.length;
        while(i--){
           s[i].classList.toggle('hidden');
        }
        this.x1+=1;
        let pid = document.getElementById('play_input');
        if (!(pid instanceof HTMLElement))
            return ;
        pid.classList.toggle('hidden');
        const cid = document.getElementById('count');
        if (!(cid instanceof HTMLElement))
            return ;
        cid.innerHTML = `${this.x1+1}/10`;
        const wid = document.getElementById('word');
        if (!(wid instanceof HTMLElement))
            return ;
        wid.innerHTML = arr[this.x1];}
        else
        {
            this.x1=0;
            const ain = document.getElementById('a');
            const pid = document.getElementById('play_input');
            if (!(pid instanceof HTMLElement))
                return ;
            if (!(ain instanceof HTMLElement))
                return ;
            new answer(ain, pid);
        }
    }
}



export class link{
    i : number;
    constructor(button: HTMLElement, t : number){
        button.addEventListener('click', ()=>this.nextLink());
        this.i = t;
    }

    nextLink(){
        let arr  = document.querySelectorAll('main');
        console.log(arr.length);
        switch(this.i){
        case(1):
            arr[0].hidden = true;
            arr[2].hidden = false;
            break;
        case(2):
            arr[2].hidden = true;
            arr[1].hidden = true;
            arr[3].hidden = true;
            arr[0].hidden = false;
            window.location.reload();
            break;
        case(3):
            arr[2].hidden = true;
            arr[0].hidden = true;
            arr[3].hidden = true;
            arr[1].hidden = false;
            break;
        case(4):
            arr[2].hidden = true;
            arr[0].hidden = true;
            arr[1].hidden = true;
            arr[3].hidden = false;
            break;
        }
    }
}



